<template>
    <div class="suggestion-page">
        <div class="container">
            <!-- 헤더 섹션 -->
            <div class="suggestion-header">
                <h1 class="page-title">건의사항</h1>
                <p class="page-subtitle">소중한 의견을 들려주세요</p>
            </div>

            <!-- 건의사항 작성 폼 -->
            <div class="suggestion-form-container">
                <form @submit.prevent="submitSuggestion" class="suggestion-form">
                    <!-- 카테고리 -->
                    <div class="form-group">
                        <label for="category" class="form-label">카테고리 <span class="required">*</span></label>
                        <select id="category" v-model="form.category" class="form-select" required>
                            <option value="">카테고리를 선택해주세요</option>
                            <option value="feature">기능 제안</option>
                            <option value="bug">버그 신고</option>
                            <option value="improvement">개선 사항</option>
                            <option value="ui">UI/UX</option>
                            <option value="etc">기타</option>
                        </select>
                    </div>

                    <!-- 제목 -->
                    <div class="form-group">
                        <label for="title" class="form-label">제목 <span class="required">*</span></label>
                        <input type="text" id="title" v-model="form.title" class="form-input"
                            placeholder="건의사항 제목을 입력해주세요" maxlength="100" required>
                        <div class="char-count">{{ form.title.length }}/100</div>
                    </div>

                    <!-- 내용 (Vue 네이티브 에디터) -->
                    <div class="form-group">
                        <label for="content" class="form-label">내용 <span class="required">*</span></label>
                        <div class="editor-container">
                            <!-- 툴바 -->
                            <div class="editor-toolbar">
                                <button type="button" @click="formatText('bold')"
                                    :class="{ active: isFormatActive('bold') }" class="toolbar-btn">
                                    <strong>B</strong>
                                </button>
                                <button type="button" @click="formatText('italic')"
                                    :class="{ active: isFormatActive('italic') }" class="toolbar-btn">
                                    <em>I</em>
                                </button>
                                <button type="button" @click="formatText('underline')"
                                    :class="{ active: isFormatActive('underline') }" class="toolbar-btn">
                                    <u>U</u>
                                </button>
                                <div class="toolbar-separator"></div>
                                <button type="button" @click="formatText('insertUnorderedList')" class="toolbar-btn">
                                    • 목록
                                </button>
                                <button type="button" @click="formatText('insertOrderedList')" class="toolbar-btn">
                                    1. 목록
                                </button>
                            </div>

                            <!-- 에디터 -->
                            <div ref="editor" class="rich-text-editor" contenteditable="true" @input="updateContent"
                                @keyup="updateContent" @paste="handlePaste" placeholder="건의사항 내용을 자세히 작성해주세요..."></div>
                        </div>
                        <div class="form-help-with-counter">
                            <span>구체적인 내용을 작성해주시면 더 빠른 처리가 가능합니다.</span>
                            <span class="char-count">{{ currentCharCount }}/500</span>
                        </div>
                    </div>


                    <!-- 첨부 이미지 -->
                    <div class="form-group">
                        <label for="images" class="form-label">첨부 이미지</label>
                        <div class="image-upload-area">
                            <input type="file" id="images" ref="fileInput" @change="handleFileUpload" multiple
                                accept="image/*" class="file-input">
                            <div class="upload-zone" @click="triggerFileInput" @drop="handleDrop" @dragover.prevent>
                                <div class="upload-icon">📷</div>
                                <div class="upload-text">
                                    <p>이미지를 드래그하거나 클릭하여 업로드</p>
                                    <small>PNG, JPG, GIF 파일 지원 (최대 5MB, 최대 3개)</small>
                                </div>
                            </div>
                        </div>

                        <!-- 업로드된 이미지 미리보기 -->
                        <div v-if="uploadedImages.length > 0" class="image-preview-container">
                            <div class="image-preview-list">
                                <div v-for="(image, index) in uploadedImages" :key="index" class="image-preview-item">
                                    <img :src="image.url" :alt="image.name" class="preview-image">
                                    <button type="button" @click="removeImage(index)" class="remove-image-btn">
                                        ×
                                    </button>
                                    <div class="image-name">{{ image.name }}</div>
                                </div>
                            </div>
                        </div>
                    </div>

                    <!-- 연락처 (선택사항) -->
                    <div class="form-group">
                        <label for="contact" class="form-label">연락처 (선택사항)</label>
                        <input type="text" id="contact" v-model="form.contact" class="form-input"
                            placeholder="답변을 받고 싶은 이메일이나 연락처를 입력해주세요">
                        <div class="form-help">답변이 필요한 경우에만 입력해주세요.</div>
                    </div>

                    <!-- 제출 버튼 -->
                    <div class="form-actions">
                        <button type="button" @click="resetForm" class="btn btn-secondary">
                            초기화
                        </button>
                        <button type="submit" class="btn btn-primary" :disabled="isSubmitting">
                            <span v-if="isSubmitting">제출 중...</span>
                            <span v-else>건의사항 제출</span>
                        </button>
                    </div>
                </form>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'Suggestion',
    computed: {
        currentCharCount() {
            return this.form.content ?
                (this.$refs.editor?.innerText?.length || 0) : 0;
        }
    },
    data() {
        return {
            form: {
                category: '',
                title: '',
                content: '',
                contact: ''
            },
            uploadedImages: [],
            isSubmitting: false
        }
    },
    methods: {
        updateContent() {
            // 순수 텍스트 길이만 체크
            const text = this.$refs.editor.innerText || '';
            if (text.length > 500) {
                // 500자를 초과하면 이전 값으로 복원 또는 자르기
                this.$refs.editor.innerText = text.slice(0, 500);
                // 커서 맨 끝으로 이동
                document.execCommand('selectAll', false, null);
                document.getSelection().collapseToEnd();
            }
            this.form.content = this.$refs.editor.innerHTML;
        },

        formatText(command) {
            document.execCommand(command, false, null);
            this.$refs.editor.focus();
            this.updateContent();
        },

        isFormatActive(command) {
            return document.queryCommandState(command);
        },

        handlePaste(event) {
            event.preventDefault();
            const text = event.clipboardData.getData('text/plain');
            document.execCommand('insertText', false, text);
        },

        triggerFileInput() {
            this.$refs.fileInput.click();
        },

        handleFileUpload(event) {
            const files = event.target.files;
            this.processFiles(files);
        },

        handleDrop(event) {
            event.preventDefault();
            const files = event.dataTransfer.files;
            this.processFiles(files);
        },

        processFiles(files) {
            if (this.uploadedImages.length + files.length > 3) {
                alert('최대 3개의 이미지만 업로드 가능합니다.');
                return;
            }

            Array.from(files).forEach(file => {
                if (file.size > 5 * 1024 * 1024) {
                    alert(`${file.name}은(는) 5MB를 초과합니다.`);
                    return;
                }

                if (!file.type.startsWith('image/')) {
                    alert(`${file.name}은(는) 이미지 파일이 아닙니다.`);
                    return;
                }

                const reader = new FileReader();
                reader.onload = (e) => {
                    this.uploadedImages.push({
                        file: file,
                        url: e.target.result,
                        name: file.name
                    });
                };
                reader.readAsDataURL(file);
            });
        },

        removeImage(index) {
            this.uploadedImages.splice(index, 1);
        },

        async submitSuggestion() {
            if (!this.validateForm()) {
                return;
            }

            this.isSubmitting = true;

            try {
                const formData = new FormData();
                formData.append('category', this.form.category);
                formData.append('title', this.form.title);
                formData.append('content', this.form.content);
                formData.append('contact', this.form.contact);

                this.uploadedImages.forEach((image, index) => {
                    formData.append(`images[${index}]`, image.file);
                });

                setTimeout(() => {
                    alert('건의사항이 성공적으로 제출되었습니다!');
                    this.resetForm();
                    this.isSubmitting = false;
                }, 1000);

            } catch (error) {
                console.error('건의사항 제출 실패:', error);
                alert('건의사항 제출에 실패했습니다. 다시 시도해주세요.');
                this.isSubmitting = false;
            }
        },

        validateForm() {
            if (!this.form.category) {
                alert('카테고리를 선택해주세요.');
                return false;
            }

            if (!this.form.title.trim()) {
                alert('제목을 입력해주세요.');
                return false;
            }

            if (!this.form.content.trim()) {
                alert('내용을 입력해주세요.');
                return false;
            }

            return true;
        },

        resetForm() {
            this.form = {
                category: '',
                title: '',
                content: '',
                contact: ''
            };
            this.uploadedImages = [];
            this.$refs.editor.innerHTML = '';

            if (this.$refs.fileInput) {
                this.$refs.fileInput.value = '';
            }
        }
    }
}
</script>

<style scoped>
.suggestion-page {
    min-height: 100vh;
    background: #f8f9fa;
    padding: 2rem 0;
}

.container {
    max-width: 900px;
    margin: 0 auto;
    padding: 0 1rem;
}

/* 헤더 */
.suggestion-header {
    text-align: center;
    margin-bottom: 3rem;
    margin-top: 60px;
    padding: 2rem 0;
}

.page-title {
    font-size: 2.5rem;
    font-weight: 300;
    color: #212529;
    margin-bottom: 0.5rem;
    letter-spacing: -0.02em;
}

.page-subtitle {
    font-size: 1.1rem;
    color: #6c757d;
    margin: 0;
}

/* 폼 컨테이너 */
.suggestion-form-container {
    background: #ffffff;
    border-radius: 12px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);
    padding: 2rem;
    margin-bottom: 2rem;
}

.suggestion-form {
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
}

/* 폼 그룹 */
.form-group {
    display: flex;
    flex-direction: column;
}

.form-label {
    font-size: 0.95rem;
    font-weight: 600;
    color: #495057;
    margin-bottom: 0.5rem;
}

.required {
    color: #dc3545;
}

/* 입력 필드 */
.form-input,
.form-select {
    width: 100%;
    padding: 0.75rem 1rem;
    border: 1px solid #e9ecef;
    border-radius: 8px;
    font-size: 0.95rem;
    background: #ffffff;
    transition: all 0.2s ease;
}

.form-input:focus,
.form-select:focus {
    outline: none;
    border-color: #6c757d;
    box-shadow: 0 0 0 3px rgba(108, 117, 125, 0.1);
}

.char-count {
    font-size: 0.8rem;
    color: #adb5bd;
    text-align: right;
    margin-top: 0.25rem;
}

.form-help {
    font-size: 0.85rem;
    color: #6c757d;
    margin-top: 0.25rem;
}

.form-help-with-counter {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 0.85rem;
    color: #6c757d;
    margin-top: 0.25rem;
}

/* 리치 텍스트 에디터 */
.editor-container {
    border: 1px solid #e9ecef;
    border-radius: 8px;
    overflow: hidden;
    background: #ffffff;
}

.editor-toolbar {
    display: flex;
    align-items: center;
    padding: 0.5rem;
    background: #f8f9fa;
    border-bottom: 1px solid #e9ecef;
    gap: 0.25rem;
}

.toolbar-btn {
    padding: 0.375rem 0.75rem;
    border: 1px solid #e9ecef;
    background: #ffffff;
    border-radius: 4px;
    cursor: pointer;
    transition: all 0.2s ease;
    font-size: 0.875rem;
}

.toolbar-btn:hover {
    background: #e9ecef;
}

.toolbar-btn.active {
    background: #6c757d;
    color: white;
    border-color: #6c757d;
}

.toolbar-separator {
    width: 1px;
    height: 24px;
    background: #e9ecef;
    margin: 0 0.5rem;
}

.rich-text-editor {
    min-height: 300px;
    padding: 1rem;
    font-size: 0.95rem;
    line-height: 1.6;
    outline: none;
    background: #ffffff;
}

.rich-text-editor:empty::before {
    content: attr(placeholder);
    color: #adb5bd;
    font-style: italic;
}

.rich-text-editor:focus {
    background: #ffffff;
}

/* 이미지 업로드 */
.image-upload-area {
    margin-top: 0.5rem;
}

.file-input {
    display: none;
}

.upload-zone {
    border: 2px dashed #e9ecef;
    border-radius: 8px;
    padding: 2rem;
    text-align: center;
    cursor: pointer;
    transition: all 0.2s ease;
    background: #fafbfc;
}

.upload-zone:hover {
    border-color: #6c757d;
    background: #f8f9fa;
}

.upload-icon {
    font-size: 3rem;
    margin-bottom: 1rem;
}

.upload-text p {
    font-size: 1rem;
    color: #495057;
    margin: 0 0 0.5rem 0;
}

.upload-text small {
    color: #6c757d;
}

/* 이미지 미리보기 */
.image-preview-container {
    margin-top: 1rem;
}

.image-preview-list {
    display: flex;
    gap: 1rem;
    flex-wrap: wrap;
}

.image-preview-item {
    position: relative;
    width: 120px;
    height: 120px;
    border-radius: 8px;
    border: 1px solid #e9ecef;
    overflow: hidden;
    background: #f8f9fa;
}

.preview-image {
    width: 100%;
    height: 80px;
    object-fit: cover;
}

.remove-image-btn {
    position: absolute;
    top: 4px;
    right: 4px;
    width: 24px;
    height: 24px;
    border-radius: 50%;
    background: rgba(220, 53, 69, 0.9);
    color: white;
    border: none;
    cursor: pointer;
    font-size: 14px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.image-name {
    padding: 0.25rem;
    font-size: 0.8rem;
    color: #495057;
    text-align: center;
    word-break: break-all;
    line-height: 1.2;
    height: 40px;
    overflow: hidden;
}

/* 버튼 */
.form-actions {
    display: flex;
    gap: 1rem;
    justify-content: flex-end;
    padding-top: 1rem;
    border-top: 1px solid #e9ecef;
}

.btn {
    padding: 0.75rem 2rem;
    border-radius: 8px;
    font-size: 0.95rem;
    font-weight: 500;
    cursor: pointer;
    transition: all 0.2s ease;
    border: none;
}

.btn-primary {
    background: #6c757d;
    color: white;
}

.btn-primary:hover:not(:disabled) {
    background: #5a6268;
    transform: translateY(-2px);
}

.btn-primary:disabled {
    background: #adb5bd;
    cursor: not-allowed;
}

.btn-secondary {
    background: #f8f9fa;
    color: #6c757d;
    border: 1px solid #e9ecef;
}

.btn-secondary:hover {
    background: #e9ecef;
    border-color: #6c757d;
}

/* 반응형 */
@media (max-width: 768px) {
    .suggestion-page {
        padding: 1rem 0;
    }

    .page-title {
        font-size: 2rem;
    }

    .suggestion-form-container {
        padding: 1.5rem;
    }

    .form-actions {
        flex-direction: column;
    }

    .btn {
        width: 100%;
    }

    .image-preview-list {
        justify-content: center;
    }

    .editor-toolbar {
        flex-wrap: wrap;
    }
}

/* 접근성 */
.form-input::placeholder,
.form-select::placeholder {
    color: #adb5bd;
}

/* 로딩 상태 */
.btn:disabled {
    opacity: 0.6;
}
</style>
